.input:focus {
}

.input {
   
    border: 1px solid #d7d7d7;
    outline: none;
    background-color: #f4f4f4;
    width: 372px;
    height: 36px;
    border-radius: 5px;
}

.inputWrapper { 
    position: relative;
}

.superInput {
    background-color: #dee0ff;
}

.errorInput {
    border : 1px solid rgb(169, 73, 73);
    background-color: rgb(255, 173, 173);
}

.errorInput:focus {
    background-color: rgb(255, 128, 128);
}

.error {
    position: absolute;
    bottom: 38px;
    left: 7px;
    color: rgb(244, 43, 43);
    
}
