
.button {
    padding: 5px 24px;
    border: none;
    background-color: #0066CC;
    border-radius: 5px;
    color: white;
}
.secondary {
    background-color: rgb(255, 255, 255);
    color: #0066CC;
    border: 1px solid #0066CC;
}
.disabled {
    opacity: 50%;
    color: #002E5C;
}
.default {
}

.red {
    background-color: #CC1439;
}

.default:hover {
    background-color: #0080FF;
}

.red:hover {
background-color: #FF1A47;
}

.secondary:hover {
background-color: #E5F0FA;
}


.default:active {
 background-color: #0059B2;
}

.secondary:active {
    background-color: #C9E5FF;
}

.red:active {
    background-color: #B21232;
  
}

.button:disabled {
    
    background-color: #004D99;
}
